
@import './assets/css/base.css';
#app{ height: 100%; width: 100%;
}
#config{ position: absolute; background-color: #ccc; height: 100%; width: 300px; right: 0; top: 0;
}
#config .title{ text-align: center;
}
#qqchat{ position: relative; top: 80px; left: 10%; background: #d5e6f6; width: 720px; height: 700px; border-radius: 5px; box-shadow: 0px 0px 10px 1px #bbb; z-index: 1000;
}
#qqchat .info{position: absolute; width: 100%; height: 105px; cursor: default; border-bottom: 1px solid #b7c8de;
}
#qqchat .info .infoPanel{height: 50px; margin: 5px 0;
}
#qqchat .info .infoPanel img{float: left; height: 50px; width: 50px; margin: 0 10px; border-radius: 5px; border:1px solid #c8d7e6;
}
#qqchat .info .infoPanel .infoName{display: inline-block; font-size: 20px; font-weight: 400; transform: scale(1.1,1.1);
}
#qqchat .info .infoPanel .infoState{font-size: 15px; line-height: 26px;
}
#qqchat .info .execute{position: absolute; right: 0; top: 0;
}
#qqchat .info .execute .item{float: right;
}
#qqchat .info .execute .item img{width: 36px; height: 36px;
}
#qqchat .info .chatTools{height: 35px; margin: 10px 5px;
}
#qqchat .info .chatTools .item{float: left; margin: 0 5px;
}
#qqchat .info .chatTools .item img{width: 32px; height: 32px;
}
#qqchat #content{position: absolute; top: 105px; height: 425px; width: 100%; overflow-y:scroll;
}
#qqchat #content .container{width: auto; margin-left: 10px; margin-right: 25px;
}
#qqchat #content .msgMore{margin: 30px 0 25px;
}
#qqchat #content .msgMore p{text-align: center; cursor: pointer; font-size: 15px; color: #2786e4;
}
#qqchat #content .msgMore p:hover{text-decoration: underline;
}
#qqchat #content .time{margin: 5px 0;
}
#qqchat #content .time p{text-align: center; font-size: 15px; color: #8b8b8b;
}
#qqchat #content .msgPanel{width: 100%; height: 50px; margin: 10px 0;
}
#qqchat #content .msgPanel .msgAvatar img{ height: 40px; width: 40px; border-radius: 4px; cursor: pointer;
}
#qqchat #content .msgPanel .msgText{ background: #78cdf8; padding: 10px; margin: 0 15px; border-radius: 10px;
}
#qqchat #content .msgPanel .msgText p{ font-size: 16px;
}
#qqchat #content .msgSend .msgAvatar{float: right;
}
#qqchat #content .msgSend .msgText{float: right;
}
#qqchat #content .msgRecv .msgAvatar{float: left;
}
#qqchat #content .msgRecv .msgText{float: left;
}
#qqchat .textArea{position: absolute; bottom: 0; height: 170px; width: 100%; border-top: 1px solid #b7c8de;
}
#qqchat .textArea .textAreaTop{ height: 30px; width: 100%; margin: 5px 0;
}
#qqchat .textArea .textAreaTop .textTools{float: left;
}
#qqchat .textArea .textAreaTop .textTools .item{float: left; margin: 0 4px 0 4px;
}
#qqchat .textArea .textAreaTop .textTools .item .aio{width: 25px; height: 25px;
}
#qqchat .textArea .textAreaTop .textTools .item .aioArrow{width: 7px; height: 7px; margin: 8px 0 10px 5px;
}
#qqchat .textArea .textAreaTop .msgMore{float: right;
}
#qqchat .textArea .textAreaTop .msgMore .item{float: left;
}
#qqchat .textArea .textAreaTop .msgMore .item .aio{width: 25px; height: 25px;
}
#qqchat .textArea .textAreaTop .msgMore .item span{font-size: 16px;
}
#qqchat .textArea .textAreaTop .msgMore .item .aioArrow{width: 7px; height: 7px; margin: 8px 5px 0 5px;
}
#qqchat .textArea .inputPanel{ height: 85px; margin-left: 5px; margin-right: 5px;
}
#qqchat .textArea .inputPanel textarea{ height: 85px; width: 100%; font-size: 15px; resize:none; border: none; background: transparent;
}
#qqchat .textArea .buttonPanel{position: absolute; bottom: 10px; right: 5px;
}
#qqchat .textArea .buttonPanel .btn{background: #069dd5; color: #fff; font-size: 14px; border: none; padding: 5px 20px; border-radius: 5px;
}
#qqchat .textArea .buttonPanel .btn:hover{background: #4bcaff;
}



